import styled from 'styled-components'

export const Button = styled.div`
    display: inline-block;
    padding: 5px 15px;
    background-color: #0f4aa9;
    color: #fff;
    font-size: 14px;
    border-radius: 2px;
    cursor: pointer;

    &:hover {
        background-color: #125ace;
    }
`
